import React, {Component} from "react"

function cssCode() {
    return localStorage.disabled ?
        "img, video, embed {  -webkit-filter: none; } ":
        "img, video, embed {  -webkit-filter: invert(100%) grayscale(100%); } ";
}


class Toggle extends Component {
    constructor(props) {
        super(props)

        this.state = {
            isDark: true,
        }
    }

    toggle() {
        this.setState({isDark: !this.state.isDark})

    }

    render() { 
        return <button onClick={() => this.toggle()}>{this.state.isDark ? "Disable Invert" : "Enable Invert"}</button>
    }

    

}

export default Toggle